import React from "react";
import './form.css'

const Form = ({handleSubmit, isOnList, isValidUrl, inputRef, setInputValue, inputValue, isError}) => {

  const handleChange = () => {
    setInputValue(inputRef.current.value)
  };

  const handlePasteUrl = async () => {
    const text = await navigator.clipboard.readText();
    setInputValue(text);
  };

  return (
    <form onSubmit={handleSubmit} className="form-wrapper ">
      <div className="wrapper">
        <div className="searchBar">
          <input
            placeholder={
              isOnList
                ? "This song is on the list"
                : isValidUrl
                ? "Provided url is not Youtube link"
                : isError
                  ? "Unexpected error"
                  : "YouTube link"
            }
            required
            ref={inputRef}
            type="url"
            className={isOnList || isValidUrl || isError ? "error-placeholder input" : "input"}
            onChange={handleChange}
            value={inputValue}
            onClick={handlePasteUrl}
          />
          <i className="bi bi-search search-icon"/>
        </div>
      </div>
    </form>
  );
};
export default Form;

