import React from "react";
import './modal.css'

const Modal = ({handleDeleteListItem, showModal, toggleModal, activeVideoId, info}) => {

  return (
    info ? (
      <div className={info ? "modal info show-modal show-info" : "modal info"}>
        <div className="modal-body info-body">
            <span>Song has been added</span>
          </div>
        </div>
    ) : (
      <>
        <div className={showModal ? "mask show-mask" : "mask"} onClick={() => toggleModal()}/>
        <div className={showModal ? "modal show-modal" : "modal"}>
          <div className="modal-body">
            <div className="modal-item" onClick={() => {
              toggleModal();
              if (activeVideoId) handleDeleteListItem(activeVideoId);
            }}>
              <i className="bi bi-trash3 me-4"/>
              <span>Delete song from the playlist</span>
            </div>
          </div>
        </div>
      </>
    )

  )
};

export default Modal;