import React, {useEffect, useRef, useState} from "react";
import YouTube from "react-youtube";
import 'bootstrap/dist/css/bootstrap.min.css'
import "bootstrap-icons/font/bootstrap-icons.css";
import "./App.css";
import './variable.css'
import Form from "./components/Form/Form";
import List from "./components/List/List";
import Modal from "./components/Modal/Modal";
import Cover from "./components/Cover/Cover"
import {DragDropContext, Droppable} from "react-beautiful-dnd";

export default function App() {
  const initalStateList = JSON.parse(localStorage.getItem("list")) || [];

  const [player, setPlayer] = useState(null);
  const [statePlayer, setStatePlayer] = useState(null);
  const [activeVideoId, setActiveVideoId] = useState(null);
  const [backgroundImageUrl, setBackgroundImageUrl] = useState(null);

  const [list, setList] = useState(initalStateList);
  const [urls, setUrls] = useState(initalStateList.map((item) => item.id));
  const [draggedList, setDraggedList] = useState({items: list});

  const [isOnList, setIsOnList] = useState(false);
  const [isValidUrl, setIsValidUrl] = useState(false);
  const [isError, setIsError] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [info, setInfo] = useState(false);

  const [inputValue, setInputValue] = useState('');

  const inputRef = useRef(null);
  const iframeRef = useRef(null);

  useEffect(() => {
    localStorage.setItem("list", JSON.stringify(list));
  }, [list]);

  useEffect(() => {
    if (activeVideoId) {
      setBackgroundImageUrl(list.filter(item => item.id === activeVideoId)[0].img);
    }
  }, [activeVideoId, statePlayer, list]);

  useEffect(() => {
    setList(draggedList.items);
  }, [draggedList]);

  useEffect(() => {
    setDraggedList({items: list});
    setUrls(list.map((item) => item.id));
  }, [list]);


  const onStateChange = (e) => {
    setStatePlayer(e.data);
    setActiveVideoId(e.target.getVideoData().video_id);
  };

  const onReady = (e) => {
    setPlayer(e.target);
  };


  const handleSubmit = async (e) => {
    try {
      e.preventDefault();
      const inputUrl = inputRef.current.value.includes("watch?")
        ? inputRef.current.value.trim().split("?v=")
        : inputRef.current.value.trim().split("/");
      const videoId = inputRef.current.value.includes("watch?")
        ? inputUrl[1].split("&")[0]
        : inputUrl[3];

      const response = await fetch(
        `https://noembed.com/embed?dataType=json&url=https://www.youtube.com/watch?v=${videoId}`
      );
      const data = await response.json();
      const title = await data.title;
      const thumb = await data.thumbnail_url;
      const validUrlRegex = /(youtu.*be.*)\/(watch\?v=|embed\/|v|shorts|)(.*?((?=[&#?])|$))/gm;

      const isDublicate = list.some((item) => item.id === videoId);
      const isValid = validUrlRegex.test(inputRef.current.value);

      if (!isDublicate && isValid && videoId && title && thumb) {
        setList([
          ...list,
          {
            id: videoId,
            title: title,
            img: thumb
          }
        ]);

        setUrls(list.map((item) => item.id));

        player.playVideo();
        player.pauseVideo();
        setInfo(true);

        setTimeout(() => setInfo(false), 2000);
      } else if (!isValid) {
        setIsValidUrl(true);
        setTimeout(() => {
          setIsValidUrl(false);
        }, 2000);
      } else if (isError) {
        setIsError(true);
        setTimeout(() => {
          setIsError(false);
        }, 2000);
      } else {
        setIsOnList(true);
        setTimeout(() => {
          setIsOnList(false);
        }, 2000);
      }
    } catch (e) {
      console.log("fetch error: ", e);
    } finally {
      setInputValue('');
      inputRef.current.blur();
    }
  };

  const handleDeleteListItem = (id) => {
    const filteredList = list.filter((item) => item.id !== id);
    setList(filteredList);
    setUrls(filteredList.map((item) => item.id));
  };

  const handlePlay = (id) => {
    if (statePlayer === 2) {
      player.playVideo();
      player.unMute();
    }
    if (statePlayer === 1) {
      player.pauseVideo();
    }

    // const newList = [
    //   ...list.filter((item) => item.id === id),
    //   ...list.filter((item) => item.id !== id)
    // ];
    //setUrls(newList.map((item) => item.id));

    const indexOfActiveSong = list.map(item => item.id).indexOf(id);
    setUrls(list.slice(indexOfActiveSong).map(item => item.id));
  };

  const toggleModal = (id) => {
    if (showModal) {
      setShowModal(false);
      setActiveVideoId(null)
    } else {
      setShowModal(true);
      if (id) setActiveVideoId(id);
    }
  };


  const reorder = (items, startIndex, endIndex) => {
    const result = Array.from(items);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
  };

  function onDragEnd(result) {
    if (!result.destination) {
      return;
    }

    if (result.destination.index === result.source.index) {
      return;
    }

    const items = reorder(
      draggedList.items,
      result.source.index,
      result.destination.index
    );

    setDraggedList({items});
    setList(draggedList.items);
  }

  return (
    <div className="App custom-container">
      <Cover backgroundImageUrl={backgroundImageUrl}/>
      <Form inputValue={inputValue} setInputValue={setInputValue} inputRef={inputRef} isOnList={isOnList}
            isError={isError}
            isValidUrl={isValidUrl} handleSubmit={handleSubmit}/>
      {list.length > 0 ? (
        <div className="iframe-wrapper" ref={iframeRef}>
          <YouTube
            id="player"
            iframeClassName="rounded-frame"
            onReady={onReady}
            onStateChange={onStateChange}
            opts={{
              playerVars: {
                autoplay: 1,
                mute: 1,
                playlist: urls.map((item) => item).join(","),
                rel: 0,
                controls: 1,
                modestbranding: 1,
              }
            }}
          />
        </div>
      ) : null}
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="playlist">
          {(provided) => (
            <div ref={provided.innerRef} {...provided.droppableProps}>
              <List iframeRef={iframeRef} toggleModal={toggleModal} handleDeleteListItem={handleDeleteListItem}
                    handlePlay={handlePlay}
                    activeVideoId={activeVideoId}
                    list={list} statePlayer={statePlayer}/>
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
      <Modal info={info} activeVideoId={activeVideoId} toggleModal={toggleModal} showModal={showModal}
             handleDeleteListItem={handleDeleteListItem}/>
    </div>
  );
}
