import Song from "../Song/Song";
import React, {useEffect, useState} from "react";
import './list.css'


const List = ({iframeRef, list, handlePlay, activeVideoId, statePlayer, handleDeleteListItem, toggleModal}) => {
  const [iframeHeight, setIframeHeight] = useState(null);

  useEffect(() => {
    function updateHeight() {
      if (iframeRef.current) {
        setIframeHeight(iframeRef.current.clientHeight);
      }
    }

    window.addEventListener('resize', updateHeight);
    updateHeight();

    return () => window.removeEventListener('resize', updateHeight);
  }, [iframeRef, iframeHeight]);

  return (
    // 120px padding with form height
    <ul className='list-container' style={{height: `calc(100vh - ${iframeHeight && iframeHeight + 120}px )`}}>
      {list.map((item, index) => (
        <Song index={index} key={item.id} toggleModal={toggleModal} item={item} handlePlay={handlePlay}
              activeVideoId={activeVideoId} statePlayer={statePlayer} handleDeleteListItem={handleDeleteListItem}/>
      ))}
    </ul>
  )
};

export default React.memo(List);